import uniPopup from "../../../components/uni-popup/uni-popup.vue"
import {
	MYINFO
} from "../../../utils/api.js"
export default {
	components:{uniPopup},
	data() {
		return {
			infoData: '',
			now: "",
		}
	},
	computed: {
		getDueTime: function() {
			var time = this.infoData.vipEndTime
			var result = String(time).split(" ")[0]
			return result
		}
	},
	onLoad() {
		uni.showToast({
			title: '加载中...',
			icon: 'none',
			mask: true
		})
		// 我的信息
		this.getMyInfo()
		this.getTime()
	},
	methods: {
		// 我的信息
		getMyInfo(){
			const params = {}
			this.$request.url_request(MYINFO, params, "GET", res => {
				this.infoData = JSON.parse(res.data).data
				uni.hideToast()
			}, err => {})
		},
		getTime() {
			var myDate = new Date();
			var year = myDate.getFullYear();
			var month = myDate.getMonth() + 1;
			var date = myDate.getDate();
			var h = myDate.getHours();
			var m = myDate.getMinutes();
			var s = myDate.getSeconds();
			this.now = year + '-' + this.conver(month) + "-" + this.conver(date) + " " + this.conver(h) + ':' + this.conver(m) +
				":" + this.conver(s);
			console.log(this.now)
		},
		conver(s) {
			return s < 10 ? '0' + s : s;
		},
		// 关闭续费
		closeRenewal(){
			this.$refs.closeRenewal.open()
		},
		close(){
			this.$refs.closeRenewal.close()
		},
		confirm(){
			this.$refs.closeRenewal.close()
			this.$refs.sureClose.open()
		},
		closeA(){
			this.$refs.sureClose.close()
		}
	}
}
